.home{
  background-color: white;
}

.home .home-content {
  background-color: white;
}
.home-content h1 {
  text-decoration: underline 5px;
  font-size: 50px;
  color: black;
  margin-left: 30px;
}

.home-content p {
  text-align: left;
  margin-top: 30px;
  margin-left: 30px;
  align-self: left;
  color: black;
  font-weight: 500;
  font-size: x-large;
}

.home-content img {
  /* filter: brightness(80%); */
  width: 500px;
  height: 400px;
  float: right;
  margin-top: -40px;
  margin-right: 70px;
}
.home-content .buttons button {
  display: inline-block;
}
.home-content .get-started {
  display: grid;
  margin-left: 250px;
  border-radius: 14px;
  background-color: rgb(8, 2, 19);
  color: white;
  font-size: 40px;
  margin-top: 60px;
  justify-content: baseline;
  margin-bottom: 30px;
}

.home-content .plans {
  margin-left: 100px;
  display: grid;
  border-radius: 14px;
  font-size: 40px;
  justify-content: baseline;
  margin-bottom: 30px;
}

.home-content .plans:hover{
  background-color: grey;
}

.home-content .get-started:hover{
  background-color: grey;
}
