.wrapper{
    
    row-gap: 1px;
    width: 83%;
    position: absolute;
    top: 60%;
    left: 17%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    grid-auto-rows: minmax(100px, auto);
    background-color: lightgray;
}
.wrapper img{
    width: 300px;
    height: 200px;
}
img.circular{
    width: 155px;
    height: 140px;
}
.scrapper{
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;   grid-column: 1 / 3;
    grid-row: 1 / 3;
    padding: 5%;
}
.model{
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    background-color: white;
    grid-column: 3;
  grid-row: 1 / 5;
  padding: 5%;
}
h3.center {
    text-align: center;
    text-decoration-line: underline;
    font-weight: 400;
}
.prediction{
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    grid-column: 1;
    background-color: white;
    grid-row: 3 / 5; 
    padding: 5%;
}
.analysis{
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    grid-column: 2;
    grid-row: 3;
    background-color: white;
    padding: 5%;
}
.clients{
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
    grid-column: 2;
    grid-row: 4;
    background-color: white;
    padding: 5%;
}
.wrapper-btn{
    background-color: black;
    color: white;
    padding: 1% 22%;
    border-radius: 25px;
}
.wrapper-btn:hover{
    transition: .7s;
    background-color: white;
    color: black;
    border: 1px solid black;
}
.btn-dashboard{
    display: grid;
    border-radius: 14px;
    font-size: 20px;
    margin-top: 10px;
    justify-content:baseline;
}
.heading {
    color: black;
    font-size: xx-large;
    font-weight: bold;
    border-bottom: 3px solid black;
    width: fit-content;
}