nav{
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  border-radius: 17px;
  background-color: white;
  position: relative;
  display: inline-block;
  width: 12%;
  height: 75vh;
  text-align: center;
  left: 4%;
  margin-top: 3%;
}
nav a:hover{
  text-decoration: none;
  color: white;
  background-color: black;
  transition: .5s;
  border-radius: 15px;
}
nav a{
  line-height: 35PX;
  display: block;
  color: black;
  text-decoration: none;
}
/* nav button{
  border: 1px solid black;
  background-color: white;
  color: black;
  position: absolute;
  bottom: 20px;
  font-size: 20px;
} */
.nav-btn {
  background-color: white;
  border: none;
  bottom: 3.5%;
  position: absolute;
  display: block;
  width: -webkit-fill-available;
}
nav img{
  margin: 15px auto;
  border-radius: 5rem;
  width: 5rem;
  height: 5rem;
}
img.icon{
  height: 25px;
  width: 25px;
  margin: 12px 3px;
}
nav button img{
  height: 25px;
  width: 25px;
  margin: 0px 5px;
}

/* first column admin name */
.first{
  width: 82%;
  padding: 24px 37px;
  background-color: white;
  position: absolute;
  top: 5%;
  left: 17.5%;
}
.first img{
  border-radius: 5rem;
  border: 1px solid grey;
  width: 5rem;
  height: 5rem;
}
.first h3{
  padding: 0px 2%;
  display: inline;
}
.first-btn{
  background-color: black;
  float: right;
  margin: -38px auto;
  color: white;
  padding: 5px 34px;
  border-radius: 7px;
  
}
.first-btn:hover{
  background-color: white;
  color: black;
  transition: .5s;
  
}
.first p{
  color: grey;
  margin: 0px 11px;
  display: inline;
  font-size: .9rem;
}
.first .data{
  margin-top: -1.5%;
  margin-left: 7.8%;
}
.task{
  margin-top: 20px;
  width: 87%;
  border: 2px solid black;
  border-radius: 20px;
}
.add-btn{
  /* background-color: rgba(24, 118, 206, 0.719); */
  background-color: white;
  float: right;
  font-size: 40px;
  float: right;
  color: black;
  border: none;
}
.check-boxes {
  margin: 2% 6%;
}
.check-boxes label{
  display: inline-grid;
  font-size: 18px;
}
.check-boxes input{
  font-size: 20px;
}
.check-boxes p{
  font-size: 13px;
  margin: auto;
}
.check-boxes hr {
  margin: auto -55px;
  border: 1px solid black;
}